<template>
  <div class="dynamic">
    <div @click="toggleCom">切换动态组件</div>
    <component :is="curComponent"></component>
  </div>
</template>

<script>
import Xin from '@/views/Xin.vue'
import Vuelab from '@/views/Vuelab.vue'
import RequestAnimationFrame from '@/components/vuecomp/requestAnimationFrame';
import DialogComp from "@/components/vuecomp/dialog";

export default {
  components: {
    Xin,
    Vuelab,
    RequestAnimationFrame
  },
  computed: {
    curComponent() {
      return this.comList[this.curIndex]
    }
  },
  data() {
    return {
      comList: Object.keys(this.$options.components),
      curIndex: 0,
      indexSetInterval: null
    }
  },
  methods: {
    toggleCom() {
      
    },
    setIndex() {
      if(this.indexSetInterval) clearInterval(this.indexSetInterval)
      const len = this.comList.length
      this.indexSetInterval = setInterval(() => {
        this.curIndex++
        if(this.curIndex >= len) this.curIndex = this.curIndex % len
        console.log(this.curIndex)
      }, 5000);
    }
  },
  mounted() {
    if(!this.indexSetInterval) this.setIndex()
  },
  destroyed() {
    if(this.indexSetInterval) clearInterval(this.indexSetInterval)
  }
}
</script>

